import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ApiUserService {

  apiUrl = "http://arjunbala.com/DFaculty/api.php/records/user";
  constructor(private _http: HttpClient) { }

  getAllUsers(){
    return this._http.get(this.apiUrl);
  }

  getUserById(userid:any){
    return this._http.get(this.apiUrl+"/"+userid);
  }

  delete(id:number){
    return this._http.delete(this.apiUrl+"/"+id);
  }

  insert(form:any){
    return this._http.post(this.apiUrl, form);
  }

  update(form:any, userid: number){
    return this._http.put(this.apiUrl+"/"+userid, form);
  }
}

