import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { observable } from 'rxjs';
import { ApiUserService } from '../api-user.service';
import { User } from '../user';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {

  userDetail : User = new User();
  userid = 0;
  radioGender = "Male";

  constructor(private _apiUser:ApiUserService, private _route:Router, private _activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    this.userid = this._activatedRoute.snapshot.params.id;
    if(this.userid==undefined){
      this.userid = 0;
    }
    else{
      let ob = this._apiUser.getUserById(this.userid);
      ob.subscribe((res:any)=>{
        this.userDetail = res;
      })
    }
  }

  addEditUser(){
    if(this.userid===0){
      let ob = this._apiUser.insert(this.userDetail);
      ob.subscribe(()=>{
      this._route.navigate(['user']);
      })
    }
    else{
      let ob = this._apiUser.update(this.userDetail,this.userid);
      ob.subscribe(()=>{
      this._route.navigate(['user']);
      })
    }
  }

}
