import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit {

  students: {name:string,rollNo:number,gender:string}[] = [];
  isForEdit = false;
  idToEdit = -1;
  txtName = " ";
  txtRollNo = " ";
  radioGender = "Male";
  isOk = false;
  isError = false;
  msg: string = "";
  myImage = [];
  
  constructor() { }

  ngOnInit(): void {
  }

  addStudents(){
    let temp = { 
                  name: this.txtName,   
                  rollNo: parseInt(this.txtRollNo),
                  gender: this.radioGender
                };
    this.students.push(temp);
    this.txtName = "";
    this.txtRollNo = "";
    this.isOk = true;
    this.msg = "Student Added Successfully..";
    setTimeout(()=>{this.isOk=false;},2000);
  }

  removeStudents(rollNo: number){
    this.students = this.students.filter(
      item => item.rollNo != rollNo
    );
    this.isOk = true;
    this.msg = "Student Deleted Successfully.."
    setTimeout(()=>{this.isOk=false;},2000);
  }

  setForEdit(s: {name: string, rollNo:number, gender: string}){
    this.txtName = s.name;
    this.txtRollNo = s.rollNo + "";
    this.radioGender = s.gender;
    this.isForEdit = true;
    this.idToEdit = this.students.findIndex(i=>i.rollNo==s.rollNo);
  }

  editStudents(){
    let temp = { 
                  name: this.txtName, 
                  rollNo: parseInt(this.txtRollNo),
                  gender: this.radioGender
                };
    this.students[this.idToEdit] = temp;
    this.isForEdit = false;
    this.idToEdit = -1;
    this.txtName = "";
    this.txtRollNo = "";
    this.isOk = true;
    this.msg = "Student Edited Successfully.."
    setTimeout(()=>{this.isOk=false;},2000);
  }

  selectImage(e: any){
    var reader = new FileReader();
    reader.onload = (f: any)=> {
      this.myImage = f.target.result;
    }
    reader.readAsDataURL(e.target.files[0]);
  }

}
