import { Route } from '@angular/compiler/src/core';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';
import { User } from '../user';

@Component({
  selector: 'app-detail-user',
  templateUrl: './detail-user.component.html',
  styleUrls: ['./detail-user.component.css']
})
export class DetailUserComponent implements OnInit {

  userDetail : User = new User();
  userid: any;

  constructor(private _activatedRoute : ActivatedRoute, private _apiUser:ApiUserService, private _route : Router) { }

  ngOnInit(): void {
    this.userid = this._activatedRoute.snapshot.params.userid;
    let ob = this._apiUser.getUserById(this.userid);
    ob.subscribe((res:any)=>{
      this.userDetail = res;
    });
  }

  deleteUser(){
    let ob = this._apiUser.delete(this.userid);
    ob.subscribe((res:any)=>{
      this._route.navigate(['user'])
    });
  }

  editUser(){
    this._route.navigate(['user/edit',this.userid])
  }

}
